  content: '';
  width: 6px;
  height: 6px;
  background-color: #f2c404;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -3px;
  margin-left: -3px;
}

.radio__checkmark {
  width: 12px;
  height: 12px;
  border: 1px solid #f2c404;
  position: absolute;
  border-radius: 50%;
  top: 50%;
  margin-top: -6px;
  left: 0;
