  justify-content: center;
  padding: 5rem 4rem;
  border-right: 1px solid #f4f4f4;
  border-bottom: 1px solid #f4f4f4;
  /*cursor: pointer;*/
}

.card_points:hover {
  box-shadow: 0 0 40px rgba(50, 41, 5, 0.15);
  position: relative;
}

.card_points:hover .btn {
  background: #f2c404;
  color: #010101;
}

.card_points .card__wrapper {
  padding-left: 1.8rem;
  text-align: left;
  position: relative;
  z-index: 2;
}

.card_points .card__img {
  max-height: 100%;
  padding: 0;
}

.card_points .card__img img {
  max-height: 100%;
  max-width: 5rem;
}

.card_points .card__header {
  min-height: auto;
  padding: 0;
  font-size: 1.4rem;
  text-align: left;
  padding-left: 1rem;
}

.card_points_big {
  height: 400px;
  flex-direction: column;
  padding: 1rem;
  position: relative;
}

.card_points_big:before {
  content: '';
  position: absolute;
  width: calc(1170px / 4 - 20px);
  height: 380px;
  top: 10px;
  left: 10px;
  border: 1px solid #f2c404;
}

.card_points_big:after {
  content: 'много баллов!';
  font-size: 1.3rem;
  font-weight: 700;
  line-height: 1.92;
  text-transform: uppercase;
  letter-spacing: 0.33px;
  padding: 0 1.5rem;
  background: #f2c404;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.card_points_big .card__wrapper {
  text-align: center;
  padding: 0;
}

.card_points_big .card__img {
  max-height: 16rem;
  margin-bottom: 3rem;
}

.card_points_big .card__img img {
  max-height: 100%;
  max-width: 100%;
}

.card_points_big .card__header {
  font-size: 1.6rem;
  padding: 0;
}

@media (max-width: 1023.98px) {
  .card_modal {
    width: 19.4rem;
  }
}

.form__text {
  width: 100%;
  border-radius: 5px;
  padding: 1.5rem 2rem;
  border: none;
  outline: none;
  background-color: #f4f4f4;
  margin-bottom: 1px;
  color: #000;
}

.form__text::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #999;
  opacity: 1;
}

.form__text::-moz-placeholder {
  /* Firefox 19+ */
  color: #999;
  opacity: 1;
}

.form__text:-ms-input-placeholder {
  /* IE 10+ */
  color: #999;
  opacity: 1;
}

.form__text select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  background: none;
  width: calc(100% + 3px);
  height: 100%;
  outline: none;
  position: relative;
  left: -3px;
  color: #999;
}

.form__text_ta {
  resize: none;
  min-height: 13em;
}

.form__label {
  color: #333;
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1.6;
}

.form__adress-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.form__adress-wrapper input {
  width: 33.1%;
  text-align: center;
  padding: 1.5rem 1.6rem;
}

.form_wish {
  padding-right: 4.5rem;
}

@media (min-width: 768px) {
  .form__adress-wrapper {
    justify-content: flex-start;
  }
  .form__adress-wrapper input {
    width: calc(25% - 1px);
    margin-right: 1px;
    padding: 1.5rem 1.2rem;
  }
  .form__adress-wrapper input:nth-of-type(4) {
    margin-right: 0;
    width: 25%;
  }
}

@media (max-width: 1023px) {
  .form_wish {
    padding-right: 0;
  }
}

@media (min-width: 1024px) {
  .form .no-gutters .form__text {
    border-right: 1px solid #fff;
  }
  .form .form__adress-wrapper {
    flex-wrap: nowrap;
  }
  .form .form__adress-wrapper input {
    /*width: 100%;*/
    width: 14.2%;
    padding: 1.5rem .5rem;
  }
  .form .form__adress-wrapper input:nth-of-type(4) {
    min-width: 119px;
  }
  .form__text_ta {
    min-height: 10rem;
  }
}

@media (min-width: 1210px) {
  .form .form__adress-wrapper input {
    width: 14.9%;
  }
}

.panel {
  background: #fff;
  padding: 3rem 5.3rem 4.5rem;
  box-shadow: 0 0 30px rgba(50, 41, 5, 0.1);
  border-radius: 5px;
}

.orders {
  box-shadow: 0 0 30px rgba(50, 41, 5, 0.1);
  border-radius: 5px;
  background-color: #ffffff;
}

.orders__block {
  padding: 3rem 5rem 3rem;
  border-bottom: 1px solid #f8f8f8;
  position: relative;
}

.orders__block_close {
  cursor: pointer;
  top: 1.5rem;
  right: 1.5rem;
  position: absolute;
  font-size: 1rem;
  opacity: .7;
}

.orders__block_close:hover {
  opacity: 1;
}

.orders__total {
  color: #999999;
  font-size: 1.3rem;
  padding: 2rem 5rem 2rem;
  background-color: #f8f8f8;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.orders-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1rem;
}

.orders-item__img {
  max-width: 18rem;
  display: block;
  margin: 0 auto 5rem;
}

.orders-item__text {
  font-size: 1.5rem;
  font-weight: 700;
  text-align: center;
}

.orders-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.orders-info__prices {
  display: flex;
  width: 100%;
  justify-content: center;
}

.orders-price {
  justify-content: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 0 2rem;
}

.orders-price > span:not(.price) {
  display: block;
  color: #999999;
  font-size: 1.3rem;
  font-weight: 400;
}

.orders-counter {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}

.orders-counter__button {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.6rem;
  line-height: 1;
  cursor: pointer;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  outline: none;
}

.orders-counter__button:hover {
  background-color: #f4f4f4;
}

.orders-counter__button_minus {
  padding-bottom: 2px;
}

.orders-counter__button_plus {
  padding-top: 1px;
}

.orders-counter__window {
  /* Style for "Прямоуголь" */
  width: 5rem;
  height: 5rem;
  margin: 0 1rem;
  border-radius: 5px;
  background-color: #f4f4f4;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  text-align: center;
  outline: none;
}

@media (min-width: 768px) {
  .orders-item {
    flex-direction: row;
    margin-bottom: 2rem;
  }
  .orders-item__img {
    max-width: 8rem;
    margin: 0 2rem 0 0;
  }
  .orders-item__text {
    text-align: left;
  }
  .orders-info {
    flex-direction: row;
    justify-content: space-between;
  }
  .orders-info__prices {
    width: auto;
  }
  .orders-counter {
    margin-bottom: 0;
  }
}

@media (min-width: 1024px) {
  .orders__block {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .orders-item {
    max-width: 32.6rem;
    margin-bottom: 0;
  }
  .orders-info {
    /*width: 55%;*/
  }
  .orders-price {
    margin: 0 2rem 0 3.5rem;
    min-width: 7.7rem;
  }
  .orders-counter {
    margin-right: 4rem;
  }
}

.modal-content {
  /*background-color: $bg-color;*/
}

.modal-header {
  border: none;
}

.modal-header .close {
  width: 4.5rem;
  height: 4.5rem;
  padding: 0;
  background-color: #f2c404;
  opacity: 1;
}

.modal-header .close span {
  font-size: 2rem;
}

.modal-footer {
  justify-content: flex-start;
  padding: 3rem 1rem;
}

.modal-lg .modal-body {
