}

.modal-lg .modal-footer {
  padding: 0;
  /*background-color: $bg-color;*/
  overflow: hidden;
}

.modal-lg .modal-footer .owl-carousel .owl-stage {
  box-shadow: none !important;
}

.modal-lg .modal-footer .owl-carousel .owl-stage-outer {
  padding-bottom: 0;
}

@media (min-width: 576px) {
  .modal-dialog {
    max-width: 570px;
  }
  .modal-body {
    padding: 1rem 5.5rem 5rem;
  }
  .modal-footer {
    padding: 3rem 5.5rem;
  }
}

@media (max-width: 1023.98px) {
  .modal-lg .modal-footer {
    justify-content: center;
    /*padding: 0 6rem;*/
  }
}

@media (min-width: 768px) {
  .modal-lg .modal-body {
    padding: 1rem 8.5rem 4.5rem;
  }
}

@media (min-width: 1210px) {
  .modal-lg {
    max-width: 1170px;
  }
}

